}

/* navbar */
.navbox {
    width: 100%;
    height: 70px;
    margin-top: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 30px;
}

.navbox .links-box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 70px;
    margin-top: 7px;
}

.navbox .logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.navbox .logo .logo-text {
    font-size: 10px;
    font-weight: 600;
    color: #F55698;
}

.navbox .logo .logo-icon {
    color: #F55698;
}

.navbox .links-box .links {
    font-size: 12px;
    font-weight: 600;
    color: #87268F;
    margin-left: 25px;
}

.navbox .links-box .links span {
    margin: 20px;
    cursor: pointer;
    /* border: 1px solid; */
}

.navbox .toggler {
    display: none;
}

@media(max-width: 768px) {
    .navbox {
        transition: all 1s ease;
        width: 100%;
        height: 70px;
        margin-top: 20px;
        position: relative;
        margin-left: 0;
        overflow: hidden;
    }

    .navbox.active {
        height: 270px;
    }

    .navbox .links-box {
        transition: all 1s ease;
        /* display: none; */
        opacity: 0;
    }

    .navbox.active .links-box.active {
        margin: 0;
        position: absolute;
        top: 70px;
        left: 10px;
        opacity: 1;
    }

    .navbox.active .links-box.active .links {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin: 0;
    }

    .navbox.active .links-box.active .links span {
        margin: 10px;
    }

    .navbox .logo {
        position: absolute;
        left: 20px;
        top: 10px;
    }

    .navbox .toggler {
        display: flex;
        position: absolute;
        top: 20px;
        right: 20px
    }
}

